.main-drop-ell {
    position: absolute;
    top: 68%;
    left: 24%;

    animation: ell 8s linear infinite;
    -webkit-animation: ell 8s linear infinite;
}

@-webkit-keyframes ell {
    from { top: 68%; left: 24%}
    to {
        top: 30%; left: 30%;
    }
}

@keyframes ell {
    from { top: 68%; left: 24%}
    to {
        top: 30%; left: 30%;
    }
}

.main-drop-ell2 {
    position: absolute;
    top: 72%;
    left: 62%;

    animation: ell2 5s linear infinite;
    -webkit-animation: ell2 5s linear infinite;
}

@-webkit-keyframes ell2 {
    from { top: 72%; left: 62%}
    to {
        top: 40%; left: 45%;
    }
}

@keyframes ell2 {
    from { top: 72%; left: 62%}
    to {
        top: 40%; left: 45%;
    }
}

.main-drop-ell3 {
    position: absolute;
    top: 86%;
    left: 53%;

    animation: ell3 7s linear infinite;
    -webkit-animation: ell3 7s linear infinite;
}

@-webkit-keyframes ell3 {
    from { top: 86%; left: 53%}
    to {
        top: 30%; left: 60%;
    }
}

@keyframes ell3 {
    from { top: 86%; left: 53%}
    to {
        top: 30%; left: 60%;
    }
}

.main-drop-ell4 {
    position: absolute;
